
body{
  padding: 60px;
}
input{
  font-size: 1.3rem;
  width: 100%;
  color:grey;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  justify-content: center;
  padding: 10px;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p {
  padding:5px;
  text-align: center;
}

.card img {
  max-height: 170px;

}
.card a {
  margin-left: 50%;
  
}

*{
  font-family: Helvetica,Arial,  sans-serif;
};

